
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import {getServiceHomePic, appUpdateSce, insertOrDelOrSort} from '@/api/home';
import draggable from 'vuedraggable'

export default {
name: 'homeManage',
components: {Swiper, SwiperItem, GroupTitle, Grid, GridItem, draggable},
data() {
return {
isManage: false,
quickMenuList: [],
commonsApplicationList: [],
Id: [],
myArray: [],
needRefresh: false,   //设置是否刷新
}
},
methods: {
manage() {
this.isManage = !this.isManage;
},
save() {
this.Id = [];
this.isManage = !this.isManage;
console.log(JSON.stringify(this.commonsApplicationList));
for (let i = 0; i < this.commonsApplicationList.length; i++) {
this.Id.push(this.commonsApplicationList[i].menuId);
}
insertOrDelOrSort({menus: this.Id}).then(response => {
if (response.data && response.data.message) {
this.$vux.toast.text("保存成功", 'middle');
this.needRefresh = true;
}
}).catch(error => {
console.log(error);
})
},

addSelect(item, index) {
if (this.isManage) {
if (item.img === "add") {
for (let i = 0; i < this.quickMenuList.categoryMenus.length; i++) {
for (let j = 0; j < this.quickMenuList.categoryMenus[i].menus.length; j++) {
if (item.menuId === this.quickMenuList.categoryMenus[i].menus[j].menuId) {
this.quickMenuList.categoryMenus[i].menus[j].img = "alreadyAdd";
}
}
}
this.quickMenuList.commonsApplication[0].menus.push(item);
}
}
},

minusSelect(item, index) {
if (this.isManage) {
this.quickMenuList.commonsApplication[0].menus.splice(index, 1);
for (let i = 0; i < this.quickMenuList.categoryMenus.length; i++) {
for (let j = 0; j < this.quickMenuList.categoryMenus[i].menus.length; j++) {
if (item.menuId === this.quickMenuList.categoryMenus[i].menus[j].menuId) {
this.quickMenuList.categoryMenus[i].menus[j].img = "add";
}
}
}
}
},
getIcon: function (key) {
return require('../assets/images/home/' + key + '.png');
},
getdata(evt) {
console.log(evt.draggedContext.index)
},
datadragEnd(evt) {
console.log('拖动前的索引 :' + evt.oldIndex);
console.log('拖动后的索引 :' + evt.newIndex);



















}
},
mounted() {

appUpdateSce({"appEquipment": "Android"}).then(response => {
if (response.data && response.data) {
this.quickMenuList = response.data.menuCateories;

this.commonsApplicationList = response.data.menuCateories.commonsApplication[0].menus;

for (let i = 0; i < this.quickMenuList.categoryMenus.length; i++) {
for (let j = 0; j < this.quickMenuList.categoryMenus[i].menus.length; j++) {
this.quickMenuList.categoryMenus[i].menus[j].img = "add";
}
}
for (let k = 0; k < this.commonsApplicationList.length; k++) {
for (let i = 0; i < this.quickMenuList.categoryMenus.length; i++) {
for (let j = 0; j < this.quickMenuList.categoryMenus[i].menus.length; j++) {
if (this.commonsApplicationList[k].menuId === this.quickMenuList.categoryMenus[i].menus[j].menuId) {
this.quickMenuList.categoryMenus[i].menus[j].img = "alreadyAdd";
}
}
}
}
}
}).catch(error => {
console.log(error);
})
},
activated() {
this.needRefresh = false;//设置是否刷新
},
deactivated: function () {
console.group('home-manage  deactivated 状态===============》');
},
beforeRouteLeave(to, from, next) {
to.params.needRefresh = this.needRefresh;
next();
}
}



